<template><div><p>This is api.</p>
<pre v-pre><code class="language-md"><span class="token comment">&lt;!-- 默认情况下，这里会被保持原样 --></span>

1 + 2 + 3 = {{ 1 + 2 + 3 }}
</code></pre>
<pre><code class="language-md:no-v-pre">&lt;!-- 这里会被 Vue 编译 --&gt;

1 + 2 + 3 = {{ 1 + 2 + 3 }}
</code></pre>
<pre><code class="language-js:no-v-pre">// 由于 JS 代码高亮，这里不会被正确编译
const onePlusTwoPlusThree = {{ 1 + 2 + 3 }}
</code></pre>
</div></template>


